.book-ticket-container {
    display: flex;
    flex-direction: row;
    width: 99%;
    min-height: 99%;
    background-color: rgb(221, 221, 221);
}

.book-action-book {
    display: flex;
    flex-direction: column;
    width: 68%;
    min-height: 100%;
    background-color: rgb(216, 216, 216);
    padding-left: 20px;
    padding-top: 20px;
}

.book-action-select-movie-rap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-right: 20px;
}

.seat-container {
    min-height: 50%;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    /* To 6 ct c chiu rng bng nhau */
    gap: 1px;
    /* Khong cch gia cc gh */
}

.seat {
    width: 50px;
    height: 50px;
    background-color: #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-weight: bold;
}

.occupied {
    background-color: #f00;
    /* Mu gh  c chn */
}

.book-action-button {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 90%;
}

.btn-book {
    background-color: #4CAF50;
    /* Mu nn xanh l cy */
    color: #fff;
    /* Mu ch trng */
    border: none;
    /* Loi b ng vin */
    padding: 10px 20px;
    /* Kch thc nt */
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    /* Bo gc nt */
}

.btn-cancel {
    background-color: #f7894a;
    /* Mu nn xanh l cy */
    color: #fff;
    /* Mu ch trng */
    border: none;
    /* Loi b ng vin */
    padding: 10px 20px;
    /* Kch thc nt */
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    /* Bo gc nt */
}

.book-ticket-content {
    width: 27%;
    min-height: 100%;
    padding: 10px;
    padding-top: 25px;
    border-radius: 55px;
    background-color: rgb(255, 255, 255);
}

.information-ticket {
    display: flex;
    flex-direction: column;
    padding: 5px;
}

.information-ticket p {
    margin-bottom: 10px;
    /* Khong cch 10px gia cc dng */
}